.App {
  text-align: center;
  padding: 20px;
  background-image: 
    linear-gradient(to bottom, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 100%), 
    url('background.png'); /* Gradient i obraz tła */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh; /* Opcjonalnie, aby upewnić się, że body zajmuje całą wysokość widoku */
  margin: 0; /* Usuwa domyślny margines */
}


.countdown-container {
  font-size: 3em;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.images-row {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-top: 20px;
}

.image-container {
  width: calc(50% - 40px); /* Dostosuj marginesy zgodnie z potrzebami */
  margin: 0 20px; /* Dostosuj aby uzyskać odpowiednie odstępy */
  position: relative;
}

.image-container img {
  width: 100%;
  height: auto;
}

.cross {
  position: absolute;
  top: 0; /* Pozycjonowanie na górze kontenera */
  left: 0; /* Pozycjonowanie na lewej krawędzi kontenera */
  width: 100%; /* Pełna szerokość kontenera */
  height: 100%; /* Pełna wysokość kontenera */
  background: rgba(255, 0, 0, 0.216); /* Czerwone tło z przeźroczystością */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10vw; /* Duży rozmiar tekstu, dostosuj wg potrzeb */
  color: rgb(255, 0, 0); /* Kolor tekstu */
  z-index: 10;
}

.fade-in {
  animation: fadeIn 20s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
  }
}


.fade-out {
  animation: fadeOut 4s forwards;
}

@keyframes fadeOut {
  to {
    opacity: 0;
    visibility: hidden;
    position: absolute;
  }
}
@media (max-width: 600px) { /* 600px to przykładowa szerokość, możesz ją dostosować */
  .countdown-container {
    font-size: 2em; /* Mniejszy rozmiar czcionki dla urządzeń mobilnych */
  }

  .images-row {
    flex-direction: column; /* Elementy w kolumnie zamiast w wierszu */
  }

  .image-container {
    width: 80%; /* Większa szerokość na mniejszych ekranach */
    margin: 0 auto; /* Centrowanie */
  }

  .cross {
    font-size: 20vw; /* Zwiększony rozmiar tekstu na mniejszych ekranach */
  }
}


.final-image-container {
  width: 100%; /* Kontener zajmuje pełną szerokość ekranu */
  overflow: hidden; /* Zapobiega wyświetlaniu się zawartości poza kontenerem */
}

.final-image-container img {
  width: 100%; /* Obraz dopasowuje się do szerokości kontenera */
  height: auto; /* Wysokość jest automatycznie dopasowywana, aby zachować proporcje */
}
